.login__img {
  width: 60%;
  text-align: center;
}

.login__img img {
  width: 70%;
  object-fit: contain;
}

.login__form {
  padding: 50px;
  background: var(--secondary-color);
  position: relative;
}

.login__container {
  box-shadow: rgba(17, 12, 46, 0.15);
  margin-top: 100px;
}

.login__container .user {
  width: 80px;
  height: 80px;
  position: absolute;
  top: -9%;
  left: 50%;
  transform: translate(-50%, -9%);
}

.login__form .user img {
  width: 100%;
}

.login__form h2 {
  color: #fff;
  text-align: center;
  margin-bottom: 2rem;
}

.login__form {
  width: 40%;
}

.login__form input {
  width: 100%;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  border: none;
  font-size: 1rem;
  color: var(--heading-color);
  margin-bottom: 10px;
}
.login__form input:focus {
  outline: none;
}
.auth__btn {
  background: var(--heading-color) !important;
  width: 100%;
  color: #fff !important;
}
.login__form p {
  font-size: 0.9rem;
  color: #fff;
  margin-top: 1.5rem;
  text-align: center;
}

.login__form p a {
  color: var(--heading-color);
  text-decoration: none;
  margin-left: 5px;
  font-weight: 500;
}

@media (max-width: 768px) {
  .login__img {
    display: none;
  }
  .login__form {
    width: 98%;
  }
}
